@import url('https://fonts.googleapis.com/css?family=Cardo|Pathway+Gothic+One');

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#6190e8), color-stop(99%, #a7bfe8));
  background: -webkit-linear-gradient(left, #6190e8 0%, #a7bfe8 99%);
  background: -o-linear-gradient(left, #6190e8 0%, #a7bfe8 99%);
  background: linear-gradient(to right, #6190e8 0%, #a7bfe8 99%);
  color: #FFFFFF;
}

h2 {
  font-family: 'Pathway Gothic One', sans-serif;
  font-weight: normal;
}



